import 'regenerator-runtime'; /* for async await transpile */
import '../styles/main.css';
import '../styles/responsive.css';
import DataSource from './data/data_source.js';
import "../scripts/components/list-resto.js";

const menu = document.querySelector("#menu");
const hero = document.querySelector(".jumbotron");
const main = document.querySelector("main");
const drawer = document.querySelector("#drawer");

document.addEventListener("DOMContentLoaded", _ => {
    const restoListElement = document.querySelector("list-resto");
    const renderResult = results => { restoListElement.restos = results };
    const fallbackResult = errormsg => { restoListElement.renderError(errormsg) };
    DataSource.getData()
        .then(renderResult)
        .catch(fallbackResult);
})

menu.addEventListener("click", function (event) {
    drawer.classList.toggle("open");
    event.stopPropagation();
});

hero.addEventListener("click", _ => {
    drawer.classList.remove("open");
});

main.addEventListener("click", _ => {
    drawer.classList.remove("open");
});

